/**
 * Formate une qualification pour l'affichage
 * @param {string} qualification - La qualification  formater
 * @returns {string} - La qualification formate
 */
export const formatQualification = (qualification) => {
  if (!qualification) return '';
  
  // Supprimer le prfixe "QUALIFICATION_" s'il existe
  if (qualification.startsWith('QUALIFICATION_')) {
    return qualification.replace('QUALIFICATION_', '');
  }
  
  return qualification;
};

/**
 * Formate une qualification pour l'affichage avec fallback
 * @param {string} qualification - La qualification  formater
 * @param {string} fallback - Valeur par dfaut si pas de qualification
 * @returns {string} - La qualification formate ou le fallback
 */
export const formatQualificationWithFallback = (qualification, fallback = 'Non dfinie') => {
  if (!qualification) return fallback;
  
  const formatted = formatQualification(qualification);
  return formatted || fallback;
};
