import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Chip,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  InputAdornment,
  Pagination,
  CircularProgress,
  Alert,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Tooltip,
  Snackbar,
  FormControlLabel,
  Checkbox
} from '@mui/material';
import {
  Search as SearchIcon,
  FilterList as FilterListIcon,
  Visibility as VisibilityIcon,
  Download as DownloadIcon,
  Delete as DeleteIcon,
  Refresh as RefreshIcon,
  Note as NoteIcon,
  Group as GroupIcon,
  AttachFile as AttachFileIcon,
  StickyNote2 as StickyNote2Icon
} from '@mui/icons-material';
import { useSelectedChurch } from '../../../hooks/useSelectedChurch';
import { useNotification } from '../../../hooks/useNotification';
import { formatDate } from '../../../utils/dateFormatter';
import { apiService } from '../../../services/apiService';
import DeleteConfirmDialog from '../../DeleteConfirmDialog';

const Testimonies = () => {
  console.log('Testimonies component rendering...');
  
  const { selectedChurch } = useSelectedChurch();
  const {
    notification,
    showSuccess,
    showError,
    hideNotification
  } = useNotification();
  const [testimonies, setTestimonies] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [showOnlyUnread, setShowOnlyUnread] = useState(false);
  const [selectedTestimony, setSelectedTestimony] = useState(null);
  const [viewDialogOpen, setViewDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [testimonyToDelete, setTestimonyToDelete] = useState(null);
  const [noteDialogOpen, setNoteDialogOpen] = useState(false);
  const [testimonyForNote, setTestimonyForNote] = useState(null);
  const [noteText, setNoteText] = useState('');

  // Catgories de tmoignages (correspondant au schma Prisma)
  const categories = [
    { value: '', label: 'Toutes les catgories' },
    { value: 'INTIMACY', label: 'Intimit avec Dieu' },
    { value: 'LEADERSHIP', label: 'Leadership' },
    { value: 'HEALING', label: 'Gurison/Sant' },
    { value: 'PROFESSIONAL', label: 'Professionnel' },
    { value: 'BUSINESS', label: 'Entreprises/Affaires' },
    { value: 'FINANCES', label: 'Finances' },
    { value: 'DELIVERANCE', label: 'Dlivrance' },
    { value: 'FAMILY', label: 'Famille' }
  ];

  // Charger les tmoignages
  const loadTestimonies = async () => {
    console.log('loadTestimonies called, selectedChurch:', selectedChurch);
    if (!selectedChurch?.id) return;
    
    setLoading(true);
    
    try {
      const params = {
        page: page.toString(),
        limit: '10',
        churchId: selectedChurch.id
      };
      
      if (searchTerm) params.search = searchTerm;
      if (selectedCategory) params.category = selectedCategory;
      if (showOnlyUnread) params.isRead = 'false';
      
      const response = await apiService.testimonies.getAll(params);
      const data = response.data;
      
      if (data.success) {
        setTestimonies(data.data.testimonies || []);
        setTotalPages(data.data.totalPages || 1);
      } else {
        showError(data.message || 'Erreur lors du chargement des tmoignages');
      }
    } catch (err) {
      showError('Erreur de connexion lors du chargement des tmoignages');
      console.error('Erreur:', err);
    } finally {
      setLoading(false);
    }
  };

  // Charger les tmoignages au montage et quand les filtres changent
  useEffect(() => {
    loadTestimonies();
  }, [selectedChurch, page, searchTerm, selectedCategory, showOnlyUnread]);

  // Gestion de la pagination
  const handlePageChange = (event, value) => {
    setPage(value);
  };

  // Gestion de la recherche
  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
    setPage(1); // Reset  la premire page
  };

  // Gestion du filtre par catgorie
  const handleCategoryChange = (event) => {
    setSelectedCategory(event.target.value);
    setPage(1); // Reset  la premire page
  };

  // Gestion du filtre non lus
  const handleUnreadFilterChange = (event) => {
    setShowOnlyUnread(event.target.checked);
    setPage(1); // Reset  la premire page
  };

  // Voir un tmoignage
  const handleViewTestimony = async (testimony) => {
    setSelectedTestimony(testimony);
    setViewDialogOpen(true);
    
    // Marquer automatiquement comme lu si ce n'est pas dj fait
    if (!testimony.isRead) {
      try {
        await apiService.testimonies.markAsRead(testimony.id);
        // Recharger la liste pour mettre  jour l'affichage
        loadTestimonies();
      } catch (err) {
        console.error('Erreur lors du marquage automatique comme lu:', err);
        // Ne pas afficher d'erreur  l'utilisateur pour cette action silencieuse
      }
    }
  };

  // Supprimer un tmoignage
  const handleOpenDeleteDialog = (testimony) => {
    setTestimonyToDelete(testimony);
    setDeleteDialogOpen(true);
  };

  const handleCloseDeleteDialog = () => {
    setDeleteDialogOpen(false);
    setTestimonyToDelete(null);
  };

  const handleConfirmDeleteTestimony = async () => {
    if (!testimonyToDelete) return;

    try {
      const response = await apiService.testimonies.delete(testimonyToDelete.id);
      const data = response.data;

      if (data.success) {
        showSuccess('Tmoignage supprim avec succs');
        loadTestimonies(); // Recharger la liste
      } else {
        showError(data.message || 'Erreur lors de la suppression');
      }
    } catch (err) {
      showError('Erreur de connexion lors de la suppression');
      console.error('Erreur:', err);
    } finally {
      handleCloseDeleteDialog();
    }
  };

  // Actualiser la liste
  const handleRefresh = () => {
    loadTestimonies();
  };


  // Ouvrir le dialogue de note
  const handleOpenNoteDialog = (testimony) => {
    setTestimonyForNote(testimony);
    setNoteText(testimony.note || '');
    setNoteDialogOpen(true);
  };

  // Fermer le dialogue de note
  const handleCloseNoteDialog = () => {
    setNoteDialogOpen(false);
    setTestimonyForNote(null);
    setNoteText('');
  };

  // Sauvegarder la note
  const handleSaveNote = async () => {
    if (!testimonyForNote) return;

    try {
      const response = await apiService.testimonies.addNote(testimonyForNote.id, noteText);
      const data = response.data;

      if (data.success) {
        showSuccess(data.message);
        loadTestimonies(); // Recharger la liste
        handleCloseNoteDialog();
      } else {
        showError(data.message || 'Erreur lors de l\'ajout de la note');
      }
    } catch (err) {
      showError('Erreur de connexion lors de l\'ajout de la note');
      console.error('Erreur:', err);
    }
  };

  // Formater le nom d'auteur (robuste)
  const formatAuthorName = (testimony) => {
    if (!testimony) return '';
    if (testimony.isAnonymous) return 'Anonyme';
    return `${testimony.firstName ?? ''} ${testimony.lastName ?? ''}`.trim() || '';
  };

  // Formater la catgorie (robuste)
  const formatCategory = (category) => {
    if (!category) return '';
    const categoryObj = categories.find(cat => cat.value === category);
    return categoryObj ? categoryObj.label : String(category);
  };

  if (!selectedChurch) {
    return (
      <Alert severity="warning">
        Veuillez slectionner une glise pour voir les tmoignages.
      </Alert>
    );
  }

  return (
    <Box>
      {/* En-tte avec titre et bouton d'actualisation */}
      <Box display="flex" justifyContent="space-between" alignItems="center" mb={3}>
        <Typography variant="h4" component="h1" gutterBottom>
        Gestion des tmoignages
        </Typography>
        <Button
          variant="outlined"
          startIcon={<RefreshIcon />}
          onClick={handleRefresh}
          disabled={loading}
        >
          Actualiser
        </Button>
      </Box>

      {/* Filtres */}
      <Card sx={{ mb: 3 }}>
        <CardContent>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs={12} md={4} sx={{ width: '30%'}}>
              <TextField
                fullWidth
                placeholder="Rechercher dans les tmoignages..."
                value={searchTerm}
                onChange={handleSearchChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
            </Grid>
            <Grid item xs={12} md={4} sx={{ width: '30%'}}>
              <FormControl fullWidth>
                <InputLabel>Catgorie</InputLabel>
                <Select
                  value={selectedCategory}
                  onChange={handleCategoryChange}
                  label="Catgorie"
                >
                  {categories.map((category) => (
                    <MenuItem key={category.value} value={category.value}>
                      {category.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} md={4} sx={{ width: '30%'}}>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={showOnlyUnread}
                    onChange={handleUnreadFilterChange}
                    color="primary"
                  />
                }
                label="Non lus uniquement"
              />
            </Grid>
            <Grid item xs={12} md={4}>
              <Box display="flex" justifyContent="center">
                <Chip
                  icon={<FilterListIcon />}
                  label={`${testimonies.length} tmoignage(s)`}
                  color="primary"
                  variant="outlined"
                />
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Liste des tmoignages */}
      {loading ? (
        <Box display="flex" justifyContent="center" py={4}>
          <CircularProgress />
        </Box>
      ) : testimonies.length === 0 ? (
        <Alert severity="info">
          Aucun tmoignage trouv pour cette glise.
        </Alert>
      ) : (
        <>
          <Grid container spacing={2}>
            {testimonies.map((testimony) => (
              <Grid item xs={12} md={6} lg={4} key={testimony.id} sx={{ width: '100%'}}>
                <Card sx={{ 
                  height: '100%', 
                  display: 'flex', 
                  flexDirection: 'column',
                  opacity: testimony.isRead ? 0.7 : 1,
                  border: testimony.isRead ? '1px solid rgb(184, 184, 185)' : '1px solid rgba(119, 64, 201, 0.77)',
                  boxShadow: testimony.isRead ? 'none' : '0 2px 8px rgba(106, 57, 182, 0.15)'
                }}>
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Box display="flex" justifyContent="space-between" alignItems="flex-start" mb={2}>
                      <Box display="flex" gap={1} alignItems="center">
                        <Chip
                          label={formatCategory(testimony.category)}
                          color="primary"
                          size="small"
                        />
                      </Box>
                      <Typography variant="body2" color="text.primary" sx={{ fontSize: '0.75rem', fontWeight: 500 }}>
                        {formatDate(testimony.createdAt)}
                      </Typography>
                    </Box>
                    
                    <Typography variant="h6" gutterBottom>
                      {formatAuthorName(testimony)}
                    </Typography>
                    
                    <Typography 
                      variant="body2" 
                      color="text.primary" 
                      sx={{ 
                        display: '-webkit-box',
                        WebkitLineClamp: 3,
                        WebkitBoxOrient: 'vertical',
                        overflow: 'hidden',
                        mb: 2
                      }}
                    >
                      {testimony.content}
                    </Typography>
                    
                    {testimony.note && (
                      <Box sx={{ 
                        bgcolor: 'rgba(106, 57, 182, 0.08)', 
                        p: 2, 
                        borderRadius: 2, 
                        mb: 2,
                        border: '1px solid',
                        borderColor: 'rgba(106, 57, 182, 0.2)',
                        position: 'relative'
                      }}>
                        <Box display="flex" alignItems="center" gap={1} mb={1}>
                          <Box sx={{ 
                            width: 20, 
                            height: 20, 
                            borderRadius: '50%', 
                            bgcolor: 'primary.main', 
                            display: 'flex', 
                            alignItems: 'center', 
                            justifyContent: 'center' 
                          }}>
                        <StickyNote2Icon sx={{ color: 'white', fontSize: '16px' }} />
                          </Box>
                          <Typography variant="subtitle2" color="primary.dark" sx={{ fontWeight: 600 }}>
                            Note
                          </Typography>
                        </Box>
                        <Typography variant="body2" color="text.primary" sx={{ 
                          lineHeight: 1.6,
                          fontStyle: 'italic',
                          pl: 3
                        }}>
                          {testimony.note}
                        </Typography>
                      </Box>
                    )}
                    
                    {testimony.network?.nom && (
                      <Chip
                        icon={<GroupIcon />}
                        label={`Rseau : ${testimony.network.nom}`}
                        size="small"
                        color="primary"
                        variant="outlined"
                        sx={{ mb: 1, fontSize: '0.75rem' }}
                      />
                    )}
                    
                    {testimony.illustrations?.length > 0 && (
                      <Chip
                        icon={<AttachFileIcon />}
                        label={`${testimony.illustrations.length} fichier(s)`}
                        size="small"
                        color="secondary"
                        variant="outlined"
                        sx={{ mb: 1, fontSize: '0.75rem' }}
                      />
                    )}
                  </CardContent>
                  
                  <Box sx={{ p: 2, pt: 0 }}>
                    <Box display="flex" justifyContent="space-between" alignItems="center">
                      <Box display="flex" gap={1}>
                        <Tooltip title="Voir le tmoignage">
                          <IconButton
                            size="small"
                            onClick={() => handleViewTestimony(testimony)}
                            sx={{
                              color: testimony.isRead ? '#666' : '#6a39b6',
                              '&:hover': {
                                backgroundColor: testimony.isRead ? 'rgba(102, 102, 102, 0.1)' : 'rgba(106, 57, 182, 0.1)'
                              }
                            }}
                          >
                            <VisibilityIcon />
                          </IconButton>
                        </Tooltip>
                        
                        
                        <Tooltip title={testimony.note ? "Modifier la note" : "Ajouter une note"}>
                          <IconButton
                            size="small"
                            color={testimony.note ? "primary" : "default"}
                            onClick={() => handleOpenNoteDialog(testimony)}
                          >
                            <NoteIcon />
                          </IconButton>
                        </Tooltip>
                      </Box>
                      
                      <Tooltip title="Supprimer">
                        <IconButton
                          size="small"
                          color="error"
                          onClick={() => handleOpenDeleteDialog(testimony)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </Tooltip>
                    </Box>
                  </Box>
                </Card>
              </Grid>
            ))}
          </Grid>

          {/* Pagination */}
          {totalPages > 1 && (
            <Box display="flex" justifyContent="center" mt={4}>
              <Pagination
                count={totalPages}
                page={page}
                onChange={handlePageChange}
                color="primary"
                size="large"
              />
            </Box>
          )}
        </>
      )}

      {/* Dialog pour voir un tmoignage */}
      <Dialog open={viewDialogOpen} onClose={() => setViewDialogOpen(false)} maxWidth="md" fullWidth>
        <DialogTitle>
          Tmoignage de {selectedTestimony && formatAuthorName(selectedTestimony)}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            {selectedTestimony && (
              <Box>
                <Box display="flex" gap={1} mb={2}>
                  <Chip
                    label={formatCategory(selectedTestimony.category)}
                    color="primary"
                    size="small"
                  />
                </Box>
                
                <Typography variant="body1" paragraph>
                  {selectedTestimony.content}
                </Typography>
                
                {selectedTestimony.network?.nom && (
                  <Chip
                    icon={<GroupIcon />}
                    label={`Rseau : ${selectedTestimony.network.nom}`}
                    size="small"
                    color="primary"
                    variant="outlined"
                    sx={{ mb: 2, fontSize: '0.75rem' }}
                  />
                )}
                
                {selectedTestimony.illustrations?.length > 0 && (
                  <Box sx={{ mb: 2 }}>
                    <Chip
                      icon={<AttachFileIcon />}
                      label={`${selectedTestimony.illustrations.length} fichier(s) joint(s)`}
                      size="small"
                      color="secondary"
                      variant="outlined"
                      sx={{ mb: 1, fontSize: '0.75rem' }}
                    />
                    <Box sx={{ mt: 1 }}>
                      {selectedTestimony.illustrations.map((file, index) => (
                        <Chip
                          key={index}
                          label={file.originalName}
                          icon={<DownloadIcon />}
                          clickable
                          onClick={() => window.open(`http://localhost:5001/uploads/testimonies/${file.fileName}`, '_blank')}
                          sx={{ mr: 1, mb: 1 }}
                        />
                      ))}
                    </Box>
                  </Box>
                )}
              </Box>
            )}
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setViewDialogOpen(false)}>
            Fermer
          </Button>
        </DialogActions>
      </Dialog>

      {/* Dialog de confirmation suppression */}
      <DeleteConfirmDialog
        open={deleteDialogOpen}
        title="Supprimer le tmoignage"
        content={testimonyToDelete ? `tes-vous sr de vouloir supprimer le tmoignage "${testimonyToDelete.title || 'sans titre'}" ?` : "tes-vous sr de vouloir supprimer ce tmoignage ?"}
        onClose={handleCloseDeleteDialog}
        onConfirm={handleConfirmDeleteTestimony}
      />

      {/* Dialog pour les notes */}
      <Dialog open={noteDialogOpen} onClose={handleCloseNoteDialog} maxWidth="md" fullWidth>
        <DialogTitle>
          {testimonyForNote?.note ? 'Modifier la note' : 'Ajouter une note'}
        </DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            {testimonyForNote && (
              <Typography variant="body2" color="text.primary" gutterBottom>
                Tmoignage de {formatAuthorName(testimonyForNote)} - {formatCategory(testimonyForNote.category)}
              </Typography>
            )}
            <TextField
              fullWidth
              multiline
              rows={6}
              value={noteText}
              onChange={(e) => setNoteText(e.target.value)}
              placeholder="Ajoutez votre note sur ce tmoignage..."
              variant="outlined"
              sx={{ mt: 2 }}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseNoteDialog}>
            Annuler
          </Button>
          {testimonyForNote?.note && (
            <Button 
              onClick={async () => {
                setNoteText('');
                try {
                  const response = await apiService.testimonies.addNote(testimonyForNote.id, '');
                  const data = response.data;
                  if (data.success) {
                    showSuccess(data.message);
                    loadTestimonies();
                    handleCloseNoteDialog();
                  } else {
                    showError(data.message || 'Erreur lors de la suppression de la note');
                  }
                } catch (err) {
                  showError('Erreur de connexion lors de la suppression de la note');
                  console.error('Erreur:', err);
                }
              }}
              color="error"
            >
              Supprimer la note
            </Button>
          )}
          <Button 
            onClick={handleSaveNote} 
            variant="contained"
            disabled={!noteText.trim()}
          >
            {testimonyForNote?.note ? 'Modifier' : 'Ajouter'}
          </Button>
        </DialogActions>
      </Dialog>

      {/* Snackbar feedback */}
      <Snackbar
        open={notification.open}
        autoHideDuration={4000}
        onClose={hideNotification}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert onClose={hideNotification} severity={notification.severity} sx={{ width: '100%' }}>
          {notification.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Testimonies;
