import authAxios from './authService';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5001';

export const chaineImpactService = {
  // Rcuprer la chane d'impact d'une glise sous forme d'arbre hirarchique
  getChaineImpact: async (churchId) => {
    try {
      const response = await authAxios.get(`/chaine-impact?church_id=${churchId}`);
      return response.data; // Retourne maintenant { success, church_id, total_nodes, tree }
    } catch (error) {
      // console.error('Erreur lors de la rcupration de la chane d\'impact:', error);
      throw error;
    }
  },

  // Rcuprer la chane d'impact d'un utilisateur spcifique
  getChaineImpactByUser: async (userId) => {
    try {
      const response = await authAxios.get(`/chaine-impact/user/${userId}`);
      return response.data;
    } catch (error) {
      // console.error('Erreur lors de la rcupration de la chane d\'impact de l\'utilisateur:', error);
      throw error;
    }
  },

  // Mettre  jour/rebtir automatiquement la chane d'impact
  updateChaineImpact: async (churchId) => {
    try {
      const response = await authAxios.post('/chaine-impact/update', { church_id: churchId });
      return response.data;
    } catch (error) {
      // console.error('Erreur lors de la mise  jour de la chane d\'impact:', error);
      throw error;
    }
  },

  // Supprimer la chane d'impact d'une glise
  deleteChaineImpact: async (churchId) => {
    try {
      const response = await authAxios.delete(`/chaine-impact/${churchId}`);
      return response.data;
    } catch (error) {
      // console.error('Erreur lors de la suppression de la chane d\'impact:', error);
      throw error;
    }
  }
};

export default chaineImpactService;
